@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.all-hostels {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-top: 65px;
  margin-bottom: 88px;
  button {
    background-color: v.$secondary;
    border: none;
    color: white;
    padding: 19px 62px 17px 50px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;

    border-radius: 15px;

    width: fit-content;

    transition: v.$transition;

    &:hover {
      background-color: v.$primary;
      border: none;
      color: white;

      transform: scale(0.9);
    }
  }
}
