@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.card {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 3px 6px #00000029;
  padding: 0;

  overflow: hidden;
  position: relative;
  img {
    width: 100%;
    height: 216px;
    object-fit: cover;

    transition: v.$transition;
  }
  .content {
    border-radius: 10px;

    transition: 0.5s ease;
    background-color: #fff;
    width: 100%;
    .info {
      display: flex;
      flex-direction: column;

      .header {
        margin-top: 10px;
        margin-bottom: 8.5px;
        padding: 7px 26.4px 7px 26px;
        border-bottom: 1px solid #00000029;
      }
      .call-to-action {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .title {
          color: v.$primary;
          padding: 7px 31px 7px 22px;
          p {
            margin: 0;
          }
        }
        .button {
          button {
            display: inline-block;
            padding: 6px 36px;
            width: fit-content;
            background-color: v.$primary;
            border: none;
            color: white;
            text-align: center;
            text-decoration: none;
            border-radius: 15px;

            transition: v.$transition;

            &:hover {
              background-color: v.$primary;
              border: none;
              color: white;
              transform: scale(0.9);
            }
          }
        }
      }
    }
  }

  &:hover {
    img {
      transform: scale(1.1);
    }

    .overlay {
      opacity: 1;
    }
  }
}

.padding {
  padding: 12.5px 5px 12.5px 17px;
}
