@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.hero-image {
  background-repeat: no-repeat;
  background-size: cover;

  padding-top: 79px;
  padding-bottom: 268px;
  padding-right: 170px;

  h3,
  p {
    margin: 0;
  }
  h3 {
    /* UI Properties */
    @include f.fontStyles(36px, 48px, 400px, #fff, "Tajawal");
    color: #ffffff;
    opacity: 1;
    margin-bottom: 15px;
  }
  p {
    @include f.fontStyles(36px, 30px, 300px, #fff, "Tajawal");

    opacity: 1;
    color: #ffffff;
  }
}

.tourism-content {
  padding-top: 60px;
  padding-bottom: 150px;
}
