@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.card {
  position: relative;
  overflow: hidden;
  box-shadow: 0px 3px 6px #00000029;

  border-radius: 10px;

  transition: v.$transition;

  width: 100%;

  .card-image {
    img {
      display: block;
      width: 100%;
      object-fit: cover;
      max-height: 380px;
      transition: v.$transition;
    }
  }

  .card-content {
    display: flex;
    flex-direction: column;
    padding: 32px 15px 0 24px;

    h4 {
      font-size: 30px;
    }
    p {
      font-size: 25px;
    }

    a {
      align-self: end;

      button {
        background-color: v.$primary;
        border: none;
        color: white;
        padding: 11px 96px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        border-radius: 50px;

        width: fit-content;

        margin-top: 43px;
        transition: v.$transition;

        align-self: flex-end;

        &:hover {
          filter: brightness(1.1);
          transform: scale(1.1);
          background-color: v.$secondary;
        }
      }
    }
  }

  &:hover {
    img {
      // transform: scale(1.2);
    }
  }
}
